<template>
	<div id="single-blog">
		<h2>{{blog.title}}</h2>
		<article>{{blog.content}}</article>
		<p>作者：{{blog.author}}</p>
		<ul>
			<li v-for="categorie in blog.categories">{{categorie}}</li>
		</ul>
	</div>
</template>
<script>
	export default {
		name: 'single-blog',
		data(){
			return {
				blog: {},
				id: this.$route.params.id
			}
		},
		created(){
			console.log(this.$route.params.id)
			this.$http.get('https://wd2091622437tspsax.wilddogio.com/post/' + this.id + '.json')
			.then((response) =>{
				// console.log(response)
				this.blog = response.body
			})
		}
	}
</script>
<style scoped>
	#single-blog{
		max-width: 960px;
		margin: 0 auto;
		padding: 20px;
		background: #eee;
		border: 1px dotted #aaa;
	}
</style>


